.card {
  width: 200px;
  padding: 10px;
  border: 1px solid #1c0a93;
  border-radius: 3px;
  transition: box-shadow 0.3s ease-in-out;
}

.card:hover {
  box-shadow: 2px 2px 0 #1c0a93;
}

.card.marked {
  border: 2px solid #1c0a93;
}

.card-img {
  display: flex;
  align-items: center;
  height: 150px;
}

.card img {
  width: 100%;
}
